
import { AngularFireDatabase,AngularFireList} from '@angular/fire/database';
import { Component, OnInit } from '@angular/core';
import { AngularFireStorage, AngularFireUploadTask,AngularFireStorageReference } from '@angular/fire/storage';
import { AngularFirestore } from '@angular/fire/firestore';




@Component({
  selector: 'app-userprofile',
  templateUrl: './userprofile.component.html',
  styleUrls: ['./userprofile.component.css']
})
export class UserprofileComponent implements OnInit {
email:string
ref:AngularFireStorageReference
task:AngularFireUploadTask

myid:string
itemList:AngularFireList<any>
  
itemArray=[]
data={
  name:"",
  telefone:"",
  herfa:"",
  text:"",
  email:""
  }
  userkey :any

  constructor(public afStorage:AngularFireStorage,public db:AngularFireDatabase) {
    this.itemList=db.list('users')
    
    this.itemList.snapshotChanges().
    subscribe(actions=>{
      actions.forEach(action=>{
       let y= action.payload.toJSON()
       y['$key']=action.key

       this.itemArray.push(y as ListItemClass)
       if(action.payload.child('uid').val()==this.myid['id']){
       this.userkey=action.key
        this.itemArray.push(y as ListItemClass)
        this.data.name=this.itemArray[0]['name']
        this.data.telefone=this.itemArray[0]['telefone']
        this.data.email=this.itemArray[0]['email']
               
                }
      })
     
      this.myid=localStorage.getItem('uid')
      console.log(this.itemArray)
    })






this.email=localStorage.getItem('email')
this.myid=localStorage.getItem('uid')

   }
   onEdit($key){

    this.data.name;//tekhouhom men form ye3ni ki nenzel yekhou el 9ima eljedida eli anna 3adeltha be3ed nerfe3ha lel cloud ye3ni firebase
    this.data.telefone;
   
    this.itemList.set(this.userkey,{
      name:this.data.name,//name lowla emte3 el firebase wethaniya eli hiya this.data.name teb3a lel form
      telefone:this.data.telefone,
     })
      
  this.itemArray=[]}



  ngOnInit() {
  }
  Upload(event){
const id=Math.random().toString(36).substring(2);
this.ref=this.afStorage.ref(id);
this.task=this.ref.put(event.target.file[0]);


  }

}
export class ListItemClass{
  $key:string;
    name:string
    telefone:string;
    herfa:string;
    text:string;
email:string;

}
