import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase,AngularFireList} from '@angular/fire/database'
import{Router}from '@angular/router'
import { Observable } from 'rxjs';
@Component({
  selector: 'app-myskills',
  templateUrl: './myskills.component.html',
  styleUrls: ['./myskills.component.css']
})
export class MyskillsComponent implements OnInit {
  itemList:AngularFireList<any>
  
  itemArray=[]

  data={
    name:"",
    telefone:"",
    herfa:"",
    text:"",
    }

myuid:any
  
  constructor(public db:AngularFireDatabase,public router:Router) { 

     this.itemList=db.list('skills')
    
    this.itemList.snapshotChanges().
    subscribe(actions=>{
      actions.forEach(action=>{
       let y= action.payload.toJSON()
       y['$key']=action.key
       this.itemArray.push(y as ListItemClass)
       
      })
     
      this.myuid=localStorage.getItem('uid')
      console.log(this.itemArray)
    })

    }

  ngOnInit() {
  }
 
  editForm($key){
for(let value of this.itemArray){
 
if(value['$key']==$key){
 console.log(value['$key'])
this.data.name=value['name'];
this.data.telefone=value['telefone'];
this.data.herfa=value['herfa'];
this.data.text=value['text'];

}
}


  }
onDelete($key){

this.itemList.remove($key)
this.itemArray=[]
}

onEdit($key){

  this.data.name;//tekhouhom men form ye3ni ki nenzel yekhou el 9ima eljedida eli anna 3adeltha be3ed nerfe3ha lel cloud ye3ni firebase
  this.data.telefone;
  this.data.herfa;
  this.data.text;
  this.itemList.set($key,{
    name:this.data.name,//name lowla emte3 el firebase wethaniya eli hiya this.data.name teb3a lel form
    telefone:this.data.telefone,
    herfa:this.data.herfa,
    text:this.data.text,})
    
this.itemArray=[]//kol ma3mel update enraja3 array ye3ni ne3mlo faskh 7atta metsirech 3ameleyete etekrara ye3ni yektebha mara okhra

    this.router.navigate(['/myskills'])

}


}
export class ListItemClass{
  $key:string;
    name:string
    telefone:string;
    herfa:string;
    text:string;


}
