import { Component, OnInit } from '@angular/core';
import{ActivatedRoute}from '@angular/router'
import { AngularFireDatabase,AngularFireList} from '@angular/fire/database'
@Component({
  selector: 'app-datails',
  templateUrl: './datails.component.html',
  styleUrls: ['./datails.component.css']
})
export class DatailsComponent implements OnInit {
id:any
itemList:AngularFireList<any>
  
  itemArray=[]
  
  data={
    name:"",
    telefone:"",
    herfa:"",
    text:"",
    email:"",
    
    }

  constructor(private route :ActivatedRoute,public db:AngularFireDatabase) {
    
    
this.route.params.subscribe(params=>{this.id=params


});

this.itemList=db.list('skills')
   
    this.itemList.snapshotChanges().
    subscribe(actions=>{
      actions.forEach(action=>{
       let y= action.payload.toJSON()
       y['$key']=action.key
       console.log(action.key)
        if(action.key==this.id['id']){
this.itemArray.push(y as ListItemClass)
this.data.name=this.itemArray[0]['name']
this.data.telefone=this.itemArray[0]['telefone']
this.data.email=this.itemArray[0]['email']
       
        }
       
      })
      console.log(this.itemArray[0])
   console.log(this.data)
    }) 



   }

  ngOnInit() {
    console.log(this.id['id'])
  }

}
export class ListItemClass{
  $key:string;
    name:string
    telefone:string;
    herfa:string;
    text:string;
email:string;

}